    :items="producers"
    :onCancel="onCancel"
    :streamId="streamId"
    :apiListKey="apiListKey"
    :service="service"
  >
  </associate-object-streams>
</template>
<script>
import AssociateObjectStreams from './associateObjectStreams.vue';
import { rfProducersService } from '../../../services/rfProducersService';

export default {
